import {AuthAPI, securityAPI, UsersAPI} from "../api/api";
import {stopSubmit} from "redux-form";

const SET_USER_DATA = 'auth/SET-USER-DATA';
const GET_CAPTCHA = 'auth/GET-CAPTCHA';

export const setUserData = (userId, email, login, isAuth) => {
    return {
        type: SET_USER_DATA,
        data: {userId, email, login, isAuth}
    }
}

export const setCaptchaUrl = (captchaUrl) => {
    return {
        type: GET_CAPTCHA,
        captchaUrl: {captchaUrl}
    }
}

let initialState = {
    userId: null,
    email: null,
    login: null,
    isAuth: false,
    captchaUrl: null

};

const authReducer = (state = initialState, action) => {

    switch (action.type) {

        case GET_CAPTCHA: return  {
            ...state,
             ...action.captchaUrl

        }

        case SET_USER_DATA: return  {

            ...state,
            ...action.data
        }

        default: return state
    }
}

export const headerLogin = () => async (dispatch) => {
    let response = await UsersAPI.getHeader();
            if (response.data.resultCode === 0) {
            let {id, email, login} = response.data.data;
            dispatch(setUserData(id, email, login, true));
        }
}

export const loginUser = (email, password, rememberMe, captcha) => async (dispatch) => {
    let response = await AuthAPI.login(email, password, rememberMe, captcha);
        if (response.data.resultCode === 0) {
            dispatch(headerLogin());
        }

        else {
                if (response.data.resultCode === 10) {
                dispatch(getCaptcha());
                 }
            let message = response.data.messages.length > 0 ? response.data.messages[0] : "Error";
            dispatch(stopSubmit("login", {_error: message}));
        }
}

export const logout = () => async (dispatch) => {
    let response = await AuthAPI.logout()
        if (response.data.resultCode === 0) {
            dispatch(setUserData(null, null, null, false));
        }
}

export const getCaptcha = () => async (dispatch) => {
    let response = await securityAPI.gerCaptchaUrl();
    let captchaUrl = response.data.url;
    dispatch(setCaptchaUrl(captchaUrl));
}

export default authReducer;
